import React from "react";

const ModalMessage = ({ message, enter, found }) => {
  return (
    <div>
      <p>{message}</p>
      {enter && (
        <p>
            <span>{enter}</span> 
        </p>
      )}
      {found && (
        <p>
           <span>{found}</span> uuid
        </p>
      )}
    </div>
  );
};

export default ModalMessage;
